/* eslint-disable */
import ng from 'vendor-bb-angular';

import bbStorageModuleKey, { bbStorageServiceKey } from 'lib-bb-storage-ng';
import savingGoalsData from './data-bb-saving-goals-http';

const savingGoalsDataModuleKey = 'data-bb-saving-goals-http-ng';

export const savingGoalsDataKey = 'data-bb-saving-goals-http-ng:savingGoalsData';

export default ng
  .module(savingGoalsDataModuleKey, [
    bbStorageModuleKey,
  ])
  .provider(savingGoalsDataKey, [() => {
    const config = {
      baseUri: '/',
    };

    return {
      setBaseUri: (baseUri) => {
        config.baseUri = baseUri;
      },
      $get: [
        '$q',
        bbStorageServiceKey,
        '$httpParamSerializer',
        /* into */
       savingGoalsData(config),
      ],
    };
  }])
  .name;



